import { DdzPlayTypeDefine } from "./DdzPlayRule";

export default class DdzPokerTypeData{
    /**
     * 
     * @param type 牌型
     * @param count 组成牌型权柄数 连对对子数 飞机数 顺子连续数 其他是牌数量
     * @param weight 牌型对应权重
     */
    constructor(public type: number = 0, public count: number = 0, public weight: number = 0){}
    public getData(){
        return [this.type, this.count, this.weight];
    }

    public isDataValid(){
        return this.type > DdzPlayTypeDefine.DDZ_NONE;
    }
}